import { columnIndices } from "./sheetHelper";
import * as CSV from "csv-string";

class Sheet {
  constructor(fileReaderResult) {
    this._sheet = CSV.parse(fileReaderResult);
  }

  getCell(cellString) {
    const letter = cellString.replace(/[^A-Za-z]/g, "").toLowerCase();
    const number = cellString.replace(/[^0-9]/g, "");

    const columnIndex = columnIndices[letter];
    const rowIndex = parseInt(number) - 1;

    return this._sheet[rowIndex][columnIndex];
  }

  getColumn(letter) {
    const sanitizedLetter = letter.replace(/[^A-Za-z]/g, "").toLowerCase();
    const columnIndex = columnIndices[sanitizedLetter];
    return this._sheet.map(row => row[columnIndex]);
  }

  // parse() {
  //   return {
  //     quarter: this.getCell("E1"),

  //   };
  // }
}

export default Sheet;
