import React from "react";
import Sheet from "../../models/Sheet";

function Report(props) {
  const { csvString, name } = props;

  React.useEffect(() => window.print());

  const sheet = new Sheet(csvString);
  const quarter = sheet.getCell("E1");
  const namesColumn = sheet.getColumn("B");

  const nameIndex = namesColumn.findIndex(nameInCell => {
    return nameInCell === name;
  });
  const rowNumber = parseInt(nameIndex) + 1;

  const variableTarget = sheet.getCell(`D${rowNumber}`);
  const value4 = sheet.getCell(`E${rowNumber}`);
  const value5 = sheet.getCell(`O${rowNumber}`);
  const value6 = sheet.getCell(`U${rowNumber}`);
  const value7 = sheet.getCell(`F${rowNumber}`);
  const value8 = sheet.getCell(`P${rowNumber}`);
  const value9 = sheet.getCell(`V${rowNumber}`);
  const value10 = sheet.getCell(`G${rowNumber}`);
  const value11 = sheet.getCell(`Q${rowNumber}`);
  const value12 = sheet.getCell(`W${rowNumber}`);
  const value13 = sheet.getCell(`X${rowNumber}`);

  return (
    <React.Fragment>
      <h2>This is a demo!</h2>
      <div>A sales person with this name was found on row {rowNumber}.</div>
      <div>Quarter: {quarter}.</div>
      <div>The demand team member's name is "{name}".</div>
      <div>Their variable target is: "{variableTarget}".</div>
      <div>Here is the table:</div>

      <table>
        <tr>
          <th>Component</th>
          <th>Component-Target-Compensation</th>
          <th>Component-Achievement-Level</th>
          <th>Payout Amount</th>
        </tr>
        <tr>
          <td>Country/Team</td>
          <td>{value4}</td>
          <td>{value5}</td>
          <td>{value6}</td>
        </tr>
        <tr>
          <td>Individual Revenue</td>
          <td>{value7}</td>
          <td>{value8}</td>
          <td>{value9}</td>
        </tr>
        <tr>
          <td>Individual Sales</td>
          <td>{value10}</td>
          <td>{value11}</td>
          <td>{value12}</td>
        </tr>
      </table>

      <div>The total gross discretionary pay is {value13}</div>
    </React.Fragment>
  );
}

export default Report;
