import React from "react";
import Report from "./Report/Report";

function App() {
  const [csvString, setCsvString] = React.useState("");
  const [salespersonName, setSalespersonName] = React.useState("");
  const [file, setFile] = React.useState(null);

  function openFile(event) {
    event.preventDefault();

    if (!file) {
      return;
    } else if (file.size > 500000) {
      alert("This file is HUGE! What are you trying to upload?");
      return;
    }

    const reader = new FileReader();
    reader.onload = function() {
      setCsvString(reader.result);
    };
    reader.readAsText(file);
  }

  if (!csvString) {
    return (
      <React.Fragment>
        <form onSubmit={openFile}>
          <input
            type="file"
            accept=".csv"
            onChange={event => setFile(event.target.files[0])}
          />
          <input
            type="text"
            placeholder="salesperson name"
            onChange={event => setSalespersonName(event.target.value)}
            value={salespersonName}
          />
          <input type="submit" value="Generate Statement" />
        </form>
      </React.Fragment>
    );
  } else {
    return <Report csvString={csvString} name={salespersonName} />;
  }
}

export default App;
